                        <h5 id="ui-id-26" class="modal-title">Add manual</h5><button type="button" title="" @click="$emit('close')" class="close" data-original-title="Close"><i class="fa fa-times" aria-hidden="true"></i></button>
                    </div>
                    <div id="backgroundPopup" class="modal-body" style="height: auto; min-height: 0px; max-height: none;">
                        <section id="main-content" class="main-content">
                            <section class="main-content">
                                <div class="container-fluid p-0">
                                    <div class="p-0">

                                        <ValidationObserver v-slot="{ handleSubmit }">
                                            <form v-on:submit.prevent="handleSubmit(submit)" ref="form">
                                                <div class="row">
                                                    <div class="col-md-6">
                                                        <div class="form-group"><label>Tracked Time:</label> 
                                                            <input type="text" placeholder="00:00:00" disabled="disabled" class="form-control">
                                                        </div>
                                                    </div>
                                                    <div class="col-md-6">
                                                        <div class="form-group">
                                                            <ValidationProvider :rules="required" v-slot="{ errors }"  >
                                                            <label>Title:<span class="text-danger">*</span></label>
                                                            <div class="input-group mb-3">
                                                                <input type="text" placeholder="Title" maxlength="100"  name="RequestTitle" :class="errors[0] ? 'form-control is-invalid' :'form-control'" >
                                                                <span>{{ errors[0] }}</span>
                                                            </div>
                                                        </ValidationProvider>
                                                        </div>
                                                    </div>
                                                    <div class="col-md-6">
                                                        <div class="form-group"><label>Time:(In Hours)<span class="text-danger">*</span></label>
                                                            <div class="input-group mb-3">
                                                                <input type="text" maxlength="4" data-vv-as="Hours" name="RequestHours" class="form-control" aria-required="true" aria-invalid="true">
                                                                <div class="invalid-feedback">Time is required.</div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <div class="col-md-6">
                                                        <div class="form-group"><label>Select:</label>
                                                            <div class="form-group pl-0 border-0">
                                                                <div class="form-check form-check-inline">
                                                                    <div class="custom-control custom-radio custom-control-inline">
                                                                        <input type="radio" name="UserType" id="rdo_0" value="1" class="form-check-input custom-control-input">
                                                                        <label class="custom-control-label" for="rdo_0"></label>
                                                                    </div> 
                                                                    <label for="UserType" class="form-check-label font-weight-normal">
                                                                        Client
                                                                    </label>
                                                                </div>
                                                                <div class="form-check form-check-inline">
                                                                    <div class="custom-control custom-radio custom-control-inline">
                                                                        <input type="radio" name="UserType" id="rdo_1" value="2" class="form-check-input custom-control-input">
                                                                        <label class="custom-control-label" for="rdo_1"></label>
                                                                    </div>
                                                                     <label for="UserType" class="form-check-label font-weight-normal">
                                                                        PM
                                                                    </label>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <div class="col-md-6">
                                                        Select 
                                                    </div>
                                                    <!---->
                                                    <div class="col-md-6">
                                                        <div class="form-group"><label>Upload a File:</label> <a href="javascript:;" data-toggle="popoveruserguide" data-content="<small class='form-text text-muted text-left'>Maximum upload size per file: 2 MB<br/>Disallowed File Types: .exe,.com,.js,.php,.asp,.aspx<br/></small>" data-original-title="" title=""><i class="fa fa-question-circle-o text-popover" style="font-size: 14px;"></i></a>
                                                            <div class="input-group">
                                                                <div class="custom-file">
                                                                    <input type="file" size="4" name="FileSrc" data-vv-as="Image" tabindex="-1" class="custom-file-inputs form-control" id="filestyle-1" style="position: absolute; clip: rect(0px, 0px, 0px, 0px);">
                                                                    <div class="bootstrap-filestyle input-group"><input type="text" class="form-control disabled" disabled="">
                                                                        <div class="input-group-append" style="cursor:pointer;">
                                                                            <span class="group-span-filestyle input-group-btn input-group-text bg-white" tabindex="0">
                                                                                <label title="Click here to select a file." for="filestyle-1" class="mb-0 font-weight-normal ">Choose File</label>
                                                                            </span><span class="input-group-text border-0 bg-transparent p-0 pl-2">
                                                                                <a href="javascript:;" class="round-icon-small btn-danger-light clsattclear" title="Clear"><i class="fa fa-undo"></i></a>
                                                                                <a href="javascript:;" class="round-icon-small btn-danger-light clsattremove d-none" title="Remove"><i class="fa fa-times"></i></a>
                                                                            </span></div>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <div class="col-md-12">
                                                        <div class="form-group">
                                                            <ValidationProvider :rules="required" v-slot="{ errors }" class="tt_textarea_span position-relative" name="Description">
                                                                <textarea v-model="manualData.trackDescription" name="Description" class="form-control tt_textarea" :class="errors[0] ? 'is-invalid' : ''" style="border-color: #BDBCBD; resize:none" :placeholder="$t('PleaseEnterDescription')" maxlength="2000" />
                                                                <span>{{ errors[0] }}</span>
                                                            </ValidationProvider>
                                                            <!---->
                                                        </div>
                                                    </div>
                                                </div>
                                               

                                                    <div class="row flex-row-reverse">

                                                        <div class="text-end text-right col-12">
                                                            <button type="submit" class="btn btn-success"><i
                                                                    class="fa fa-save pr-2"></i>Save</button>
                                                            <button @click="$emit('close')" type="button"
                                                                class="btn btn-danger" style="margin-left: 5px;"><i
                                                                    class="fa fa-close pr-2"></i>Cancel</button>
